@import "../../App.scss";

.visible--beyond-mobile {
    display: block;
}

.visible--beyond-mobile {
    display: none;
}

.title {
    text-align: center;
    font-weight: 600;
    font-size: 1.125rem;
    color: $lapis_medium_light;
}

.date_location {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;

    p {
        margin: .125rem 0;
        color: $black_light;
        font-weight: 600;
        width: 100%;
    }
}

.faint, .faint a {
    color: $black_light;
    padding: 0;
    margin: 0;
    font-weight: 600;
}

.program_header {
    color: $black;
    font-weight: 600;
    width: fit-content;
    margin: 0;
    margin-bottom: 0;
    padding: 0;
}

.program_link {
    color: $black;
}

