.solver_link {

    &:hover {
        cursor: pointer;
        text-decoration: underline;
    }
}

table {
    width: 100%;
}

tr, td {
    text-align: left;  
    width: 100%;
}


.time {
    display: table-cell;
    vertical-align: top;
    width: 30%;
    border-bottom: 1px solid $black;
    border-right: 1px solid $black;
    display: none;
}

.program_description {
    display: table-cell;
    vertical-align: top;
    width: 80%;
    border-bottom: 1px solid $black;
    padding: 1rem;


    h4 {
        font-weight: 600;
        font-size: 1.125rem;
        margin: 0;

        .date {
            font-weight: 400;
        }
    }

    // a {
    //     color: $black;
    // }

    .program_details {
        // display: flex;
        // justify-content: space-between;
        // flex-wrap: wrap;
        // margin: .5rem 0;

        // span {
        //     margin-top: 0;
        // }

        // a {
        //     color: $black_light;
        // }
    }
}

.lightning_talk {
    margin: 2.5rem 0rem;

    p {
        margin: .5rem 0;
    }

    .talk_title {
        font-weight: 600;
        color: $orange_web;
        margin: 0;
        padding: 0;
    }
}

tr, td {
    padding: 1rem;
}

@media(min-width: 748px) {

    .visible--beyond-mobile {
        display: inline;
    }

    .visible--only-mobile {
        display: none;
    }

    .time {
        display: table-cell;
        width: 20%;
    }

    .date_location {
    
        p {
            width: 50%;
            text-align: right;
        }
    }
}
